export default function Router() {
    return (
        <HashRouter basename='/portfolio'>
            <Header />
            <Navbar />
            <Routes>
                <Route path="" element={<HomePage />} />
                <Route path="tic-tac-toe" element={<TicTacToe />} />
                <Route path="*" element={<p>Page not found.</p>} />
            </Routes>

            <Footer />
        </HashRouter>
    );
}